import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate, Link } from 'react-router-dom';
import { AppBar, Toolbar, Button, Typography, Container, Box, Paper, TextField } from '@mui/material';
import PostCreate from './packages/PostCreate';
import PostList from './packages/PostList';
import PostDetail from './packages/PostDetail';
import PostEdit from './packages/PostEdit';
import Login from './components/Login';
import Signup from './components/Signup';

function App() {
    const [isAuthenticated, setIsAuthenticated] = useState(false);

    useEffect(() => {
        const token = localStorage.getItem('accessToken');
        setIsAuthenticated(!!token);
    }, []);

    const handleLogout = () => {
        localStorage.removeItem('accessToken');
        localStorage.removeItem('refreshToken');
        setIsAuthenticated(false);
    };

    const ProtectedRoute = ({ children }) => {
        return isAuthenticated ? children : <Navigate to="/login" />;
    };

    return (
        <Router>
            <AppBar position="static">
                <Toolbar>
                    <Typography variant="h6" sx={{ flexGrow: 1 }}> </Typography>
                    {isAuthenticated ? (
                        <>
                            <Button color="inherit" component={Link} to="/posts/create"> </Button>
                            <Button color="inherit" component={Link} to="/posts"> </Button>
                            <Button color="inherit" onClick={handleLogout}></Button>
                        </>
                    ) : (
                        <>
                            <Button color="inherit" component={Link} to="/login"></Button>
                            <Button color="inherit" component={Link} to="/signup"></Button>
                        </>
                    )}
                </Toolbar>
            </AppBar>
            <Container sx={{ mt: 4 }}>
                <Routes>
                    <Route path="/" element={<Typography variant="h4" align="center">    </Typography>} />
                    <Route path="/login" element={<Login setIsAuthenticated={setIsAuthenticated} />} />
                    <Route path="/signup" element={<Signup />} />
                    <Route path="/posts/create" element={<ProtectedRoute><PostCreate /></ProtectedRoute>} />
                    <Route path="/posts" element={<PostList />} />
                    <Route path="/posts/:id" element={<PostDetail />} />
                    <Route path="/posts/edit/:id" element={<ProtectedRoute><PostEdit /></ProtectedRoute>} />
                </Routes>
            </Container>
        </Router>
    );
}

export default App;
